// iobs
function iobs(aladdin) {
    this._aladdin = aladdin;
}

// name
readonlyProperty(iobs.prototype, 'name', 'iobs');

/**
 * 
 *@param opts         option
 *         option.type      string    base64 | filePath
 *         option.fileData  string     typebase64base64
 *         option.fileName  string     typebase64
 *         option.filePath  string     typefilePath
 *         option.bucket    string    bucket, iobs-zhike-dev-pri
 *         option.key       string     bucket  key
 *         option.token     string    token
 *         option.onprocess     function    
 *  @param cb(err, data)         
 // err, {code: "", message: ""}
 // data , {status:"", process:0.12}
 // data.status (0: , 1: , 2: , 4: )
 // data.process 
 */
iobs.prototype.upload = function(opts, cb) {
    if (aladdin.env.isiOS) {
        var url = App.Api.iobsHost + '/upload/' + opts.bucket + '/' + opts.key;
        aladdin.http.upload({
            url: url,
            filePath: opts.filePath,
            fileDataName: 'file',
            body: {
                token: opts.token,
                bucket: opts.bucket,
                key: opts.key
            },
            onProcess: opts.onProcess
        }, cb);
        return;
    }

    opts = opts || {};
    opts.onprocess && (opts.onprocess.keep = true);

    opts.bucket = opts.bucket || 'iobs-sf-dev-pri';
    this._aladdin.call(this.name, 'upload', opts, cb);
};

/**
 * 
 *@param opts         option
 *         option.url url
 *  @param cb(err, data)         
 // err, {code: "", message: ""}
 // data ,
 //    data.filePath 
 */
iobs.prototype.download = function(opts, cb) {
    opts = opts || {};
    this._aladdin.call(this.name, 'download', opts, cb);
};

